ul.topnav {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background: rgb(1, 1, 10);
  color: white;
  padding: 1rem;
  position: fixed;
    top: 0;
    width: 100%;
    z-index:1000;
}

ul.topnav li {
  float: left;
}

ul.topnav li a {
  display: block;
  color: white;
  font-size: 1.5rem;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}
ul.topnav li button {
  display: block;
  border: none;
  color: white;
  font-size: 1.5rem;
  text-align: center;
  padding: 12px 14px;
  text-decoration: none;
  background-color: rgb(1, 0, 13);
  border: none;
  margin-top: 18px;
  width:40px;
  height:40px;
  background: none;
}
.fa-sun{
  font-size: 30px;
  
}
.fa-moon{
  font-size: 30px;
  color:white;
}

ul.topnav li a:hover:not(.active) {
  /* background-color: rgb(248, 242, 242); */
  text-decoration: underline;
  cursor: pointer;
  font-weight: bolder;
}

ul.topnav li a.active {
  background-color: #2baa04;
}

ul.topnav li.right {
  float: right;
}
